import React, { useMemo, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { LabelsList } from 'components/molecules';
import { searchSubTasksByLabelAction } from 'reducers/subTasksReducer/actions';

const FiltersPanel = () => {
  // TODO: fetch labels list via API
  const labels = useMemo(() => ['ut', 'sed', 'sunt', 'commodi', 'dolores']); // useSelector(labelsSelector);
  const dispatch = useDispatch();
  const onLabelClick = useCallback(
    label => dispatch(searchSubTasksByLabelAction(label)),
    [dispatch, searchSubTasksByLabelAction],
  );

  return (
    <div>
      <LabelsList labels={labels} onLabelClick={onLabelClick} />
    </div>
  );
};

export default FiltersPanel;
